import * as React from 'react';
import Box from '@mui/material/Box';
import Drawer from '@mui/material/Drawer';
import CssBaseline from '@mui/material/CssBaseline';
import Toolbar from '@mui/material/Toolbar';
import List from '@mui/material/List';
import Divider from '@mui/material/Divider';
import ListItem from '@mui/material/ListItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import SettingsIcon from '@mui/icons-material/Settings';
import TableChartIcon from '@mui/icons-material/TableChart';
import AddchartIcon from '@mui/icons-material/Addchart';
import PagesIcon from '@mui/icons-material/Pages';
import SentimentVerySatisfiedIcon from '@mui/icons-material/SentimentVerySatisfied';

const drawerWidth = 240;

 function PermanentDrawerLeft() {
  return (
    <Box sx={{ display: 'flex' }}>
      <CssBaseline />     
      <Drawer
        sx={{
          width: drawerWidth,
          flexShrink: 0,
          '& .MuiDrawer-paper': {
            width: drawerWidth,
            boxSizing: 'border-box',
          },
        }}
        variant="permanent"
        anchor="left"
      >
        <Toolbar />
        <span className="sb_admin"><SentimentVerySatisfiedIcon/>  SB ADMIN</span>
        <Divider />
        <List>          
        <ListItem button key ="Component">
             <ListItemIcon><SettingsIcon/></ListItemIcon>
             <ListItemText primary={"Component"} />
             </ListItem>
        </List>
        <Divider />
        <List>        

         <ListItem button key ="Pages">
             <ListItemIcon><PagesIcon/></ListItemIcon>
             <ListItemText primary={"Pages"} />
             </ListItem>

         <ListItem button key ="Tables">
             <ListItemIcon><TableChartIcon/></ListItemIcon>
             <ListItemText primary={"Tables"} />
             </ListItem>
        
        <ListItem button key ="Charts">
             <ListItemIcon><AddchartIcon/></ListItemIcon>
             <ListItemText primary={"Charts"} />
             </ListItem>
        </List>        
      </Drawer>      
    </Box>
  );
}
export default PermanentDrawerLeft